import { Controller } from '@ali/4ever-cangjie';

type Handler = {
  enable: () => void;
  disable: () => void;
  clear: () => void;
};

export type LeftToolbarItem = (controller: Controller) => Handler;

export const leftToolbarHandlers: LeftToolbarItem[] = [];

const getLeftToolbarHandler = (controller: Controller) => {
  for (const handlerGetter of leftToolbarHandlers) {
    const res = handlerGetter(controller);
    if (res) {
      return res;
    }
  }
  return null;
};

export const handleDragChange = (
  action: 'clear' | 'disable' | 'enable',
  controller: Controller,
) => {
  const leftToolbar = getLeftToolbarHandler(controller);
  if (!leftToolbar) {
    return {};
  }

  leftToolbar[action]?.();
  return {};
};
